.col { padding-left: 15px; padding-right: 15px; }

.text-center { text-align: center; }

.nowrap { white-space: nowrap; }

.scroll-locked { overflow: hidden; }

.shadow-2 { box-shadow: 0 1px 1px rgba(0, 0, 0, 0.12), 0 2px 2px rgba(0, 0, 0, 0.12); }

.shadow-3 { box-shadow: 0 1px 1px rgba(0, 0, 0, 0.12), 0 2px 2px rgba(0, 0, 0, 0.12), 0 4px 4px rgba(0, 0, 0, 0.12); }

.shadow-4 { box-shadow: 0 1px 1px rgba(0, 0, 0, 0.12), 0 2px 2px rgba(0, 0, 0, 0.12), 0 4px 4px rgba(0, 0, 0, 0.12), 0 8px 8px rgba(0, 0, 0, 0.12); }

.shadow-5 { box-shadow: 0 1px 1px rgba(0, 0, 0, 0.12), 0 2px 2px rgba(0, 0, 0, 0.12), 0 4px 4px rgba(0, 0, 0, 0.12), 0 8px 8px rgba(0, 0, 0, 0.12), 0 16px 16px rgba(0, 0, 0, 0.12); }

@media screen and (max-width: 1640px) { .xl-block { display: block; }
  .xl-flex { display: flex; }
  .xl-none { display: none; }
  .xl-text-center { text-align: center; } }

@media screen and (max-width: 1279px) { .lg-block { display: block; }
  .lg-flex { display: flex; }
  .lg-inline-flex { display: inline-flex; }
  .lg-none { display: none; } }

@media screen and (max-width: 1100px) { .md-block { display: block; }
  .md-flex { display: flex; }
  .md-none { display: none; } }

@media screen and (max-width: 768px) { .sm-block { display: block; }
  .sm-flex { display: flex; }
  .sm-none { display: none; } }

.loader { position: absolute; width: 100%; height: 100%; top: 0; left: 0; display: flex; justify-content: center; flex-direction: column; align-items: center; z-index: -100; opacity: 0; transition: opacity .2s; background: rgba(255, 255, 255, 0.6); }

.loader .is-active { z-index: 110; opacity: 1; }

.loader-content { width: 45px; aspect-ratio: .75; --c: no-repeat linear-gradient(#000 0 0); background: var(--c) 0% 50%, var(--c) 50% 50%, var(--c) 100% 50%; animation: b7 1s infinite linear alternate; }

@keyframes b7 { 0% { background-size: 20% 50%, 20% 50%, 20% 50%; }
  20% { background-size: 20% 20%, 20% 50%, 20% 50%; }
  40% { background-size: 20% 100%, 20% 20%, 20% 50%; }
  60% { background-size: 20% 50%, 20% 100%, 20% 20%; }
  80% { background-size: 20% 50%, 20% 50%, 20% 100%; }
  100% { background-size: 20% 50%, 20% 50%, 20% 50%; } }
